/* Responsive Table*/

@media(max-width: 550px) {
    .table thead {
        display: none !important;
    }

    .table, .table tbody, .table tr, .table td{
        display: block;
        width: 100%;
    }

    .table tr{
        margin-bottom: 15px !important;
        border-bottom: solid 1px rgba(0, 0, 0, 0.2);
    }

    .table td{
        text-align: right;
        padding-left: 50%;
        position: relative;
        border: none !important;
    }

    .table td::before{
        content: attr(data-label);
        position: absolute;
        left: 0 !important;
        text-align: left;
        width: 50%;
        font-size: 15px !important;
        font-weight: 700;
        padding-left: 15px;
    }

}

